.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
}

.empty-message {
    font-size: 18px;
    margin: 50px auto;
}

.cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
}

button {
    margin-top: auto;
}

::-webkit-scrollbar{
    width: 4px;
    background-color: transparent;
  }

::-webkit-scrollbar-thumb{
    background-color: rgba(0,0,0,0.7);
    border-radius: 20px;
}
