.menu-item {
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden;
}

.menu-item:hover{
  opacity: 0.9;
  cursor: pointer;
}

.menu-item:hover .background-image{
  transform: scale(1.1);
  transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}

.menu-item:hover .content {
  background: white;

}

.menu-item .background-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}

.menu-item .content{
  height: 90px;
  padding: 0 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0.7;
  position: absolute;
  background: white;

}

.menu-item .title {
  font-weight: bold;
  margin: 0 6px 0;
  font-size: 22px;
  color: black;
  text-align: center;
}

.menu-item .subtitle {
  color: black;
}

@media screen and (max-width: 800px) {
  .menu-item{
    height: 200px;
  }

  .menu-item .content{
    width: 30%;
    background: transparent;
  }

  .menu-item:hover .content {
    background: transparent;

  }

  .menu-item .title{
    font-size:20px;
    color: white;
  }

  .menu-item .subtitle{
    color: white;
    display: none;
  }

}
