.collection-preview {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.collection-preview .title {
    font-size: 28px;
    margin-bottom: 25px;
    cursor: pointer;
}

.collection-preview .preview {
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width: 800px) {
  .collection-preview .preview {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15px;
  }
}
