.header{
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
}

.logo-container{
  height: 100%;
  width: 70px;
  padding: 25px;
}

.options{
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.option{
  padding: 10px 15px;
  cursor: pointer;
}

.logo-container{
  display: flex;
  align-items: center;
}

.logo-container i{
  font-size: 2rem;
  padding-bottom: 5px;
}

@media screen and (max-width: 800px) {
.header{
  height: 60px;
  margin-bottom: 20px;
}

.logo-container{
  width: 50px;
  padding: 10px;
}

.options{
  width:100%;
}

.option{
  padding: 10px 5px;
}
}
